import Image from "next/image";

interface TrinityLogoProps {
  size?: "sm" | "md" | "lg";
  className?: string;
}

export function TrinityLogo({ size = "md", className = "" }: TrinityLogoProps) {
  const sizes = {
    sm: 32,
    md: 48,
    lg: 64,
  };

  const containerSizes = {
    sm: "w-10 h-10",
    md: "w-14 h-14",
    lg: "w-20 h-20",
  };

  return (
    <div className={`${containerSizes[size]} rounded-full bg-white flex items-center justify-center ${className}`}>
      <Image
        src="/trinity-logo.png"
        alt="Trinity Logo"
        width={sizes[size]}
        height={sizes[size]}
        className="object-contain"
        priority
      />
    </div>
  );
}
