"use client";

import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Header } from "@/components/header";
import { useRouter } from "next/navigation";
import { useState } from "react";

// Mock data for seats
const generateSeats = () => {
  const seats = [];
  for (let row = 1; row <= 10; row++) {
    for (let col of ["A", "B", "", "C", "D"]) {
      if (col) {
        seats.push({
          id: `${row}${col}`,
          number: `${row}${col}`,
          isAvailable: Math.random() > 0.3,
          price: 25.00,
        });
      }
    }
  }
  return seats;
};

export default function SelectSeatPage({ params }: { params: { id: string } }) {
  const router = useRouter();
  const [selectedSeats, setSelectedSeats] = useState<string[]>([]);
  const seats = generateSeats();

  const toggleSeat = (seatId: string) => {
    setSelectedSeats(prev => 
      prev.includes(seatId) 
        ? prev.filter(id => id !== seatId)
        : [...prev, seatId]
    );
  };

  return (
    <div className="min-h-screen bg-background">
      <Header title="Select Seats" showBack />
      <div className="bg-primary text-white px-6 pb-6">
        {/* Journey Summary */}
        <div className="flex justify-between items-center">
          <div>
            <p className="text-sm opacity-90">New York</p>
            <p className="font-semibold">08:00 AM</p>
          </div>
          <div className="text-center">
            <p className="text-sm opacity-90">Duration</p>
            <p className="font-semibold">3h</p>
          </div>
          <div className="text-right">
            <p className="text-sm opacity-90">Boston</p>
            <p className="font-semibold">11:00 AM</p>
          </div>
        </div>
      </div>

      {/* Seat Legend */}
      <div className="p-4 bg-white border-b">
        <div className="flex justify-around">
          <div className="flex items-center space-x-2">
            <div className="w-6 h-6 bg-primary/20 rounded"></div>
            <span className="text-sm">Available</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-6 h-6 bg-gray-300 rounded"></div>
            <span className="text-sm">Booked</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-6 h-6 bg-primary rounded"></div>
            <span className="text-sm">Selected</span>
          </div>
        </div>
      </div>

      {/* Bus Layout */}
      <div className="p-6">
        <Card className="p-4">
          {/* Driver's cabin */}
          <div className="w-16 h-12 bg-gray-200 rounded-lg mb-6 mx-auto"></div>

          {/* Seats */}
          <div className="grid grid-cols-5 gap-3 max-w-md mx-auto">
            {seats.map((seat) => (
              seat.number.endsWith("") ? (
                <div key={`space-${seat.id}`} className="w-8 h-8"></div>
              ) : (
                <button
                  key={seat.id}
                  className={`w-8 h-8 rounded ${
                    selectedSeats.includes(seat.id)
                      ? "bg-primary text-white"
                      : seat.isAvailable
                      ? "bg-primary/20 hover:bg-primary/30"
                      : "bg-gray-300 cursor-not-allowed"
                  }`}
                  onClick={() => seat.isAvailable && toggleSeat(seat.id)}
                  disabled={!seat.isAvailable}
                >
                  {seat.number}
                </button>
              )
            ))}
          </div>
        </Card>
      </div>

      {/* Bottom Action */}
      {selectedSeats.length > 0 && (
        <div className="fixed bottom-0 left-0 right-0 p-4 bg-white border-t">
          <div className="flex justify-between items-center mb-4">
            <div>
              <p className="text-sm text-muted-foreground">
                {selectedSeats.length} {selectedSeats.length === 1 ? 'seat' : 'seats'} selected
              </p>
              <p className="font-bold text-lg">
                ${(selectedSeats.length * 25).toFixed(2)}
              </p>
            </div>
            <Button 
              className="bg-primary"
              onClick={() => router.push('/payment')}
            >
              Continue
            </Button>
          </div>
        </div>
      )}
    </div>
  );
}
